.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__link-img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__title {
  font-weight: 500;
  font-size: inherit;
  margin: 0 0 4px;
  line-height: 18px;
  text-decoration: none;
  display: block;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-reviews {
  margin: 0;
  font-size: 10px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin: 0 0 16px;
  line-height: 18px;
  display: flex;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button-buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-inline: auto;
  font-weight: 700;
}

.card__button-buy:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 .stars__star {
  background-image: url("star-active.edda4c43.svg");
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

body {
  margin: 0;
}
